import React, { Component } from 'react';
import './App.css';
import { Route, Switch, Redirect } from 'react-router-dom';
import Navbar from './components/common/Navbars';
import NotFound from './components/common/NotFound';
import HomePage from './components/home/HomePage';
import UsersPage from './components/users/UsersPage';
import DetailUserPage from './components/users/DetailUserPage';

class App extends Component {
  render() {
    return (
      <div className="container">
        <Navbar />
        <div className="mt-3">
          <Switch>
            <Route exact path='/' component={HomePage} />
            <Route exact path='/user' component={UsersPage} />
            <Route exact path='/user/:id([\d]+)' component={DetailUserPage} />
            <Redirect exact from='*' component={NotFound} />
          </Switch>
        </div>
      </div>
    );
  }
}

export default App;
