import React, { Component } from 'react';
import RandomPicture from './../common/RandomPicture';

class DetailUser extends Component {
  render() {
    const { address, company, ...user } = this.props.response

    return (
      <React.Fragment>
        <div className="container emp-profile">
          <form method="post">
            <div className="row">
              <div className="col-md-4">
                <div className="profile-img">
                  <RandomPicture width="200" height="200" />
                </div>
              </div>
              <div className="col-md-6">
                <div className="profile-head">
                  <h5>
                    {user.name}
                  </h5>
                  <h6>Web Developer and Designer</h6>
                  <p className="proile-rating">RANKINGS : <span>8/10</span></p>
                  <ul className="nav nav-tabs" id="myTab" role="tablist">
                    <li className="nav-item">
                      <a className="nav-link active" id="home-tab" data-toggle="tab" href={"#home"} role="tab" aria-controls="home" aria-selected="true">About</a>
                    </li>
                    <li className="nav-item">
                      <a className="nav-link" id="address-tab" data-toggle="tab" href={"#address"} role="tab" aria-controls="address" aria-selected="false">Address</a>
                    </li>
                    <li className="nav-item">
                      <a className="nav-link" id="company-tab" data-toggle="tab" href={"#company"} role="tab" aria-controls="company" aria-selected="false">Company</a>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col-md-4">
                <div className="profile-work">
                  <p>WORK LINK</p>
                  <a href="{null}" >Bootsnipp Profile</a><br />
                  <a href="{null}" >Bootply Profile</a><br />
                  <a rel="noopener noreferrer" href={"http://" + user.website} target="_blank">Website Link</a><br />
                </div>
              </div>
              <div className="col-md-8">
                <div className="tab-content profile-tab" id="myTabContent">
                  <div className="tab-pane fade show active" id="home" role="tabpanel" aria-labelledby="home-tab">
                    <div className="row">
                      <div className="col-md-6">
                        <label>User Id</label>
                      </div>
                      <div className="col-md-6">
                        <p>{user.id}</p>
                      </div>
                    </div>
                    <div className="row">
                      <div className="col-md-6">
                        <label>Name</label>
                      </div>
                      <div className="col-md-6">
                        <p>{user.name}</p>
                      </div>
                    </div>
                    <div className="row">
                      <div className="col-md-6">
                        <label>Email</label>
                      </div>
                      <div className="col-md-6">
                        <p>{user.email}</p>
                      </div>
                    </div>
                    <div className="row">
                      <div className="col-md-6">
                        <label>Phone</label>
                      </div>
                      <div className="col-md-6">
                        <p>{user.phone}</p>
                      </div>
                    </div>
                    <div className="row">
                      <div className="col-md-6">
                        <label>Profession</label>
                      </div>
                      <div className="col-md-6">
                        <p>Web Developer and Designer</p>
                      </div>
                    </div>
                  </div>
                  {/* End of about tab */}
                  <div className="tab-pane fade" id="address" role="tabpanel" aria-labelledby="address-tab">
                    <div className="row">
                      <div className="col-md-6">
                        <label>Street</label>
                      </div>
                      <div className="col-md-6">
                        <p>{address.street}</p>
                      </div>
                    </div>
                    <div className="row">
                      <div className="col-md-6">
                        <label>Suite</label>
                      </div>
                      <div className="col-md-6">
                        <p>{address.suite}</p>
                      </div>
                    </div>
                    <div className="row">
                      <div className="col-md-6">
                        <label>City</label>
                      </div>
                      <div className="col-md-6">
                        <p>{address.city}</p>
                      </div>
                    </div>
                    <div className="row">
                      <div className="col-md-6">
                        <label>Zipcode</label>
                      </div>
                      <div className="col-md-6">
                        <p>{address.zipcode}</p>
                      </div>
                    </div>
                  </div>
                  {/* End of address tab */}
                  <div className="tab-pane fade" id="company" role="tabpanel" aria-labelledby="company-tab">
                    <div className="row">
                      <div className="col-md-6">
                        <label>Name</label>
                      </div>
                      <div className="col-md-6">
                        <p>{company.name}</p>
                      </div>
                    </div>
                    <div className="row">
                      <div className="col-md-6">
                        <label>Catch Phrase</label>
                      </div>
                      <div className="col-md-6">
                        <p>{company.catchPhrase}</p>
                      </div>
                    </div>
                    <div className="row">
                      <div className="col-md-6">
                        <label>Bs</label>
                      </div>
                      <div className="col-md-6">
                        <p>{company.bs}</p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </form>
        </div>
      </React.Fragment>
    )
  }
}

export default DetailUser