import React, { Component } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import DetailUser from './DetailUser';
import NotFound from './../common/NotFound';

class DetailUserPage extends Component {
  constructor(props) {
    super(props);

    this.paramID = props.match.params.id;
    this.state = {
      response: {},
      loaded: false,
      userNotFound: false
    }
  }

  componentDidMount() {
    const url = 'https://jsonplaceholder.typicode.com/users/' + this.paramID;

    axios.get(url, {
      headers: {
        "Accept": 'application/json',
        "Content-type": "application/json"
      }
    })
      .then(res => {
        if (res.data.length !== 0) {
          this.setState({ response: res.data, loaded: true });
        }
      })
      .catch(err => { this.setState({ userNotFound: true, loaded: true }); console.log(err); })
  }

  render() {
    return (
      <React.Fragment>
        <Link to="/user"> &laquo; Back to list user</Link>
        {
          (this.state.loaded) ? (
            (Object.entries(this.state.response).length !== 0) ?
              <DetailUser response={this.state.response} /> :
              <NotFound />
          ) : null
        }
      </React.Fragment>
    )
  }
}

export default DetailUserPage;